import React from 'react'
import '../MyComponents/style.css'
import {Country} from '../MyComponents/country'
import axios from 'axios';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

export const Content = (props) => {

  function findnow(){
    if(document.getElementById("cntry").value==="Default"){
      toast.error("Please choose a country!")
      return;
    }
    
    var date=document.getElementById("date").value
    var finddate = new Date();
    var dd,mm,yyyy;
    if(date==="T"){
      const ele = document.getElementById("cntry");
      if(ele==null) return;
      const option=ele.value.replaceAll(' ','');
     const ele2=document.getElementById(option);
      const clr=ele2.style.backgroundColor;
      ele2.style.backgroundColor = "white";
      ele2.style.animation = "glow 4s 1";
      ele2.style.backgroundColor = clr;
      ele2.scrollIntoView()
      return;
    }
    else if(date==="Y"){
      finddate. setDate(finddate. getDate() - 1);
      dd = String(finddate.getDate()).padStart(2, '0');
      mm = String(finddate.getMonth() + 1).padStart(2, '0');
      yyyy = finddate.getFullYear();
      date= yyyy + '-' + mm + '-' + dd;
    }
    else{
      dd = String(finddate.getDate()).padStart(2, '0');
      mm = String(finddate.getMonth() + 1).padStart(2, '0');
      yyyy = finddate.getFullYear();
      date= (yyyy-1) + '-' + mm + '-' + dd;
    }
    var cntry=document.getElementById("cntry").value.toLowerCase()
    var options = {
      method: 'GET',
      url: 'https://covid-193.p.rapidapi.com/history',
      params: {country: cntry, day: date},
      headers: {
        'x-rapidapi-host': 'covid-193.p.rapidapi.com',
        'x-rapidapi-key': '7bbbf7ef36msh496831d8995bb5cp167ac0jsn1214f1d375f4'
      }
    };
    
    axios.request(options).then(function (response) {
      var data=response.data.response[response.data.response.length-1]
      var type=document.getElementById("type").innerHTML.toLowerCase()
      // console.log(response.data.response);
      toast.info(data.country+" : "+data.cases[type]+" "+type+" cases on "+date+"!")
      // alert(data.country+" : "+data.cases[type]+" "+type+" cases on "+date+"!")
    }).catch(function (error) {
      console.error(error);
    });
  }
   return (
    <div class="main-body">
      <ToastContainer 
        position="bottom-center"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        toastStyle={{ backgroundColor: "blue",color:"white" }}
        />
        <div id="selectcnt">
          <div></div>
            <select id="cntry">
                <option value="Default" >Choose Country</option>
                {props.country.length===0?<option>Waiting!</option>:
                props.country.map(function(object, i){
                  return (<option key={"cntry"+String(i)}>{object}</option>);
                })}
            </select>
            <button onClick={(e)=>{e.preventDefault();findnow();}}>Find</button>
          <div></div>
        </div>
        <div id="extras">
            <select id="date">
                <option value="T">Today</option>
                <option value="Y">Yesterday</option>
                <option value="L">Last Year</option>
            </select>
            <select onChange={(e)=>{e.preventDefault();props.update(e.target.value);}}>
                <option value="Active">Active</option>
                <option value="Critical">Critical</option>
                <option value="New">New</option>
                <option value="Recovered">Recovered</option>
                <option value="Total">Total</option>
              </select>
        </div>
      <table id="Table">
        <tr>
          <th>Countries</th>
          <th id="type">Active</th>
        </tr>
        {props.data.length===0?<h4>Waiting!</h4>:
            props.data.map((condata,i)=>{
                return (
                <Country key={"cntryno"+String(i)} country={condata.country} data={condata.data} />)
            })
            }
      </table>
    </div>
    )
}
