import './App.css';
import {Upperpart} from './MyComponents/upperpart';
import {Content} from './MyComponents/allcountries';
import {useState,useEffect} from 'react'
import axios from 'axios';

function App() {
  const [response, setresponse] = useState(null)
  const [data, setdata] = useState([])
  const [cntries, setcntries] = useState([])
  const [type, settype] = useState("active")
  useEffect(()=>{
  axios.request({
    method: 'GET',
    url: 'https://covid-193.p.rapidapi.com/statistics',
    headers: {
      'x-rapidapi-host': 'covid-193.p.rapidapi.com',
      'x-rapidapi-key': '7bbbf7ef36msh496831d8995bb5cp167ac0jsn1214f1d375f4'
    }
  })
  .then(function (res) {
	  setresponse(res.data.response);
    let alldata=[],cntries=[];
    for(var i=0;i<res.data.response.length;i++){
      alldata.push({
        "country":res.data.response[i].country,
        "data":(response[i].cases[type.toLowerCase()]?response[i].cases[type.toLowerCase()]:"NA")
      });
      cntries.push(response[i].country)
    }
    cntries.sort()
    setcntries(cntries)
    setdata(alldata)
  }).catch(function (error) {
	  console.error(error);
  });
  },[response])

  const update=(type)=>{
    settype(type.toLowerCase())
    let alldata=[];
    for(var i=0;i<response.length;i++){
      alldata.push({
        "country":response[i].country,
        "data":(response[i].cases[type.toLowerCase()]?response[i].cases[type.toLowerCase()]:"NA")
      });
      
    }
    setdata(alldata)
    document.getElementById("type").innerHTML=type
  }
  return (
    <>
    <Upperpart/>
    <Content data={data} country={cntries} update={update}/>
    <button id="topbtn" onClick={()=>{document.getElementById("Header").scrollIntoView()}}>Top &#8607;</button>
    </>
  );
}

export default App;
